.about-container {
  font-family: "Segoe UI", sans-serif;
  color: #333;
  line-height: 1.8;
  padding: 50px 10%;
  background: #fafaf7;
}

.about-hero {
  text-align: center;
  margin-bottom: 40px;
}

.about-hero h1 {
  font-size: 2.5rem;
  color: #2ea246;
  margin-bottom: 15px;
}

.about-hero h1 span {
  color: #43c55b;
}

.about-hero p {
  font-size: 1.1rem;
  color: #555;
}

.about-section {
  margin-bottom: 40px;
  background: #fff;
  padding: 25px;
  border-radius: 12px;
  box-shadow: 0 6px 16px rgba(0, 0, 0, 0.08);
  transition: transform 0.3s ease;
}

.about-section:hover {
  transform: translateY(-5px);
}

.about-section h2 {
  color: #2ea246;
  margin-bottom: 15px;
  font-size: 1.6rem;
  border-left: 5px solid #43c55b;
  padding-left: 10px;
}

.about-section p {
  font-size: 1rem;
  color: #444;
}

.about-section blockquote {
  margin-top: 15px;
  font-style: italic;
  color: #2ea246;
  background: #f0fdf4;
  padding: 12px 18px;
  border-radius: 8px;
  border-left: 5px solid #43c55b;
}

.about-section ul {
  list-style: none;
  padding: 0;
}

.about-section ul li {
  margin: 8px 0;
  padding-left: 28px;
  position: relative;
}

.about-section ul li::before {
  content: "🥛";
  position: absolute;
  left: 0;
  top: 0;
}

.products ul li::before {
  content: "🌿";
}

.why-choose ul li::before {
  content: "✔";
  color: #2ea246;
  font-weight: bold;
}
